import React from "react"
import PropTypes from "prop-types"
import { graphql, useStaticQuery, Link } from "gatsby"
import classNames from "classnames"

import GridContainer from "@components/Grid/GridContainer.js"
import GridItem from "@components/Grid/GridItem.js"

import { H3, H6 } from "@components/header/header.js"

import GridList from "@material-ui/core/GridList"
import GridListTile from "@material-ui/core/GridListTile"
import GridListTileBar from "@material-ui/core/GridListTileBar"

import ThreeMysticApesUtil from "three_mystic_apes_extension/util/utilities.js"

import Moment from "moment"

BlogSeries.propTypes = {
  props: PropTypes.object,
}

export default function BlogSeries({ children, pageNode, location, ...props }) {
  const themeVariation =
    typeof window !== "undefined"
      ? localStorage.getItem("themeVariation")
        ? localStorage.getItem("themeVariation")
        : "dark"
      : "dark"
  const postGroup = "series"

  const postGroupValue =
    props.data === null || typeof props.data === "undefined" ? "" : props.data
  const postDynamicTitle =
    props.dynamicTitle === null || typeof props.dynamicTitle === "undefined"
      ? ""
      : props.dynamicTitle

  const { className } = props
  const { sectionNodes } = useStaticQuery(
    graphql`
      query {
        sectionNodes: allThreeMysticApesPostSeries {
          edges {
            node {
              name
              slug
              display
            }
          }
        }
      }
    `
  )
  const postListGrid = "postListGrid"

  const postsGridListClassNames = classNames({
    [postListGrid]: true,
    [themeVariation]: true,
    [className]: className,
  })

  if (postGroupValue === "") {
    return (
      <GridContainer justify="center">
        {sectionNodes.edges.map(({ node }, index) => (
          <GridItem cs={12} sm={6} md={4} key={index}>
            <Link
              to={`/blog/${postGroup.toLowerCase()}/${node.slug}`}
              title={node.display}
              aria-label={node.display}
            >
              <div>{node.display}</div>
            </Link>
          </GridItem>
        ))}
      </GridContainer>
    )
  }

  return (
    <GridContainer justify="center">
      <GridItem cs={12} sm={12} md={12}>
        <GridList cellHeight={180}>
          {props.posts.edges.map(({ node }, index) => (
            <GridListTile key={index} className={postsGridListClassNames}>
              <Link
                to={node.slug}
                title={node.post.title}
                aria-label={node.post.ariaLabel}
                state={{
                  crum: {
                    pageNode: pageNode,
                    locationSlug: ThreeMysticApesUtil.TrimEnd(
                      location.pathname,
                      "/\\\\"
                    ),
                    postGroupValue,
                    postDynamicTitle,
                  },
                }}
              >
                <img src={node.post.hero.publicURL} alt={node.post.title} />
                <GridListTileBar
                  title={<H3>{node.post.title}</H3>}
                  subtitle={
                    <div>
                      <H6>{node.post.excerpt}</H6>
                      <div>{Moment(node.post.startDate).format("d MMM")}</div>
                    </div>
                  }
                />
              </Link>
            </GridListTile>
          ))}
        </GridList>
      </GridItem>
    </GridContainer>
  )
}
